import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { TranslateConfigService } from '../../services/translate-config.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { UtilService } from 'src/providers/utils/utils.service';
import { MenuOptions } from 'src/app/app.component';
import { LoggerServiceProvider } from 'src/providers/logger-service/logger-service';
import { ConfigProvider } from 'src/providers/utils/config';
import { PopoverController, NavController } from '@ionic/angular';
import { PopoverContentPage } from '../popover-content/popover-content.page';
import { LoginFirstPage } from './../login-first/login-first.page';
import { ResetPasswordPersonalService } from './resetPasswordPersonalService';
import { SecurityQuestionsService } from '../sign-up/securityQuestionsService';
import { SignUpServiceProvider } from '../sign-up/sign-up-service';
import { AlertController } from '@ionic/angular';
import * as HttpStatus from 'http-status-codes';

@Component({
  selector: 'app-reset-pwd-with-personal-details',
  templateUrl: './reset-pwd-with-personal-details.page.html',
  styleUrls: ['./reset-pwd-with-personal-details.page.scss'],

})
export class ResetPwdWithPersonalDetailsPage implements OnInit {
  selectedLanguage:string;
  personalDetailsForm: FormGroup;
  SecretForm: FormGroup;
  email: any;
  isSecret: boolean;
  questions: any;
  // questions: any=[{
  //   type:"name",
  //   Question:"What is your name",
  //   Answer:"sample"
  // },
  // {
  //   type:"age",
  //   Question:"What is your age",
  //   Answer:"30"
  // }];
  str1: any = "one";
  selectOptions: any;
  isShowingLoader:boolean = true;
  errorMsg :any;
  selectedValue: any;
  constructor(
    public translate:TranslateService,private translateConfigService: TranslateConfigService,
    public utils: UtilService,public logger:LoggerServiceProvider, public popOverCtrl: PopoverController,
    private config: ConfigProvider, public forgotpasswordService: ResetPasswordPersonalService,
    public securityQuesService: SecurityQuestionsService, public signUpService: SignUpServiceProvider,
    private alertCtrl : AlertController, private router: Router
  ) {
    this.selectedLanguage = this.translateConfigService.getDefaultLanguage();
    this.translateConfigService.setLanguage('en');
    this.email = history.state.email;
    this.isSecret = history.state.isSecret;
    this.selectOptions = {
      cssClass: "selectPopover-forgotPassword"
    };
    console.log('this.isSecret',this.isSecret);
   }
  //  ionViewDidLoad() {

  // }

  ngOnInit() {

      this.personalDetailsForm = new FormGroup({
      identityNumber: new FormControl('', [Validators.required, Validators.minLength(3)]),
      policyNo: new FormControl('', [Validators.required, Validators.minLength(3)])

    });
    this.SecretForm = new FormGroup({
      securityQuestion: new FormControl('', [Validators.required]),
      answer: new FormControl('', [Validators.required, Validators.minLength(1)])

    });
    this.logger.log('ionViewDidLoad ResetPwdWithPersonalDetailsPage');
    if (this.isSecret) {
      this.requestForSecurityQuestions();
    }
    // this.questions=[{
    //   id:1,
    //   type:"name",
    //   question:"What is your name What is your name",
    //   Answer:"sample"
    // },
    // {
    //   id:2,
    //   type:"age",
    //   question:"What is your age",
    //   Answer:"30"
    // }];
    // this.selectOptions=this.questions[0];
  }
  ionViewWillEnter() {
    this.utils.component.emit(MenuOptions.FORGOT_PASSWORD);
  }

  //function to get the secret questions
  requestForSecurityQuestions = function () {
    if (this.utils.isDeviceOnLine()) {
      this.securityQuesService.getSecurityQuestions().then(
        (result) => {
          this.questions = result.responseJSON.array;
          this.SecretForm.controls['securityQuestion'].setValue(this.questions[0]['Type'], { onlySelf: true });
          this.isShowingLoader=false;
        }, (err) => {
           this.isShowingLoader=false;
        });
    } else {
      let errorMsg = this.translate.instant('generic_msgs.network_err');
      this.utils.showAlert("", errorMsg);
    }
  }

  //function to check teh user policy number
  personalDetailscheck = function () {
    if (this.utils.isDeviceOnLine()) {
      var identityNumber = this.personalDetailsForm.controls.identityNumber.value;
      var policyNo = this.personalDetailsForm.controls.policyNo.value;
      this.utils.showProgressLoader();
      this.forgotpasswordService.resetPasswordPersonal(this.email, identityNumber, policyNo).then(
        (result) => {
          this.serviceSuccess(result);
        }, (err) => {
          this.serviceFailed(err);
        });
    } else {
      let errorMsg = this.translate.instant('generic_msgs.network_err');
      this.utils.showAlert("", errorMsg);
    }
  }

  //function to validate the user secret question and answer
  secretQuestioncheck = function () {
    if (this.utils.isDeviceOnLine()) {
      var securityQuestion = this.SecretForm.controls.securityQuestion.value;
      var answer = this.SecretForm.controls.answer.value;
      this.utils.showProgressLoader();
      this.forgotpasswordService.resetPasswordSecret(this.email, securityQuestion, answer).then(
        (result) => {
          this.serviceSuccess(result);
        }, (err) => {
          this.serviceFailed(err);
        });
    } else {
      let errorMsg = this.translate.instant('generic_msgs.network_err');
      this.utils.showAlert("", errorMsg);
    }
  }


  /**
   * This method is used to show Pop over
   * @param myEvent - Element that is clicked
   */
  async openPopup(e: Event) {
    const popover = await this.popOverCtrl.create({
      component: PopoverContentPage,
      showBackdrop: false,
      event: e,
      componentProps: { msg: this.str1,  cssClass: 'custom-popover' },
      cssClass: 'custom-popover'
    });

    await popover.present();

  }

 

  serviceSuccess(result){
    try{
      this.utils.dissmisProgressLoader();
      this.utils.showAlert("", result.responseJSON.userMessage);
      this.router.navigate(['/login-first']);
    }catch(e){
      this.utils.dissmisProgressLoader();
      let errorMsg = this.translate.instant('generic_msgs.server_err');
      this.utils.showAlert("", errorMsg);
    }
  }

  serviceFailed(err){
    this.utils.dissmisProgressLoader();
    let errorMsg = this.utils.handleServiceException(err); //common error handler
    this.utils.showAlert("", errorMsg);
  }

    forgotPolicyNumber(){

    
     var identityNo = this.personalDetailsForm.controls.identityNumber.value;
     alert(identityNo)
     if (!identityNo){
       this.errorMsg=this.translate.instant('Signup.identityNo_req');
      this.showDialog("", this.errorMsg);
      }
     else {
       if (identityNo.length>2){
         //this.showDialog("", "Identity No should be greater than 2 characters");
        this.utils.showProgressLoader();
         var userRegistered = true;
         var mobileOs = "AOS";
         if (this.utils.getPlatform()=='ios'){
            mobileOs = "IOS";
         }
         this.signUpService.postForgotPolicyNumber(identityNo,userRegistered,mobileOs).then(
          (res) => {
            try{
               if (res['responseJSON'] && res['responseJSON']['statusCode'] == HttpStatus.ACCEPTED) {
                this.errorMsg = res['responseJSON']['userMessage'] ;
                this.utils.dissmisProgressLoader();
               // this.showDialog("", this.errorMsg);
               this.utils.showAlert("",this.errorMsg);

              }
            }catch(e){
              this.utils.dissmisProgressLoader();let errorMsg = this.translate.instant('generic_msgs.server_err');
              this.utils.showAlert("", errorMsg);
            }
          }, (err) => {
            this.utils.dissmisProgressLoader();
            let errorMsg = this.utils.handleServiceException(err);
            this.utils.showAlert("", errorMsg);
          });
       }
     }


  }
      /**
   *
   * @param title - this is the title of the alert
   * @param message  - message that need to display on the alert.
   */
  showDialog(title: string, message: string) {
    let buttons = [{
         text: 'Ok',
         handler: () => {
            //this.navCtrl.pop();
         }
     }];

    let alert = this.alertCtrl.create({
       //title: title,
       message: message,
       buttons: buttons,
       //enableBackdropDismiss: false,
    });
   // return (alert).present();
  }


}
